	export default {
		data() {
		    return {
				vaule:'',
				// 热门商品
				hotShop:[],
				// 加载更多是否显示
				isShow:true,
				// 获取活动列表
				activeList:[],
				//轮播图
				swiper:[],
				 indicatorDots: true,
			
				//活动
				status: {
					poper: '最新活动',
					shop: '热门商品',
					more: '更多活动'
				}
		    }
		},
		
		
 		onLoad() {
			// 获取最热门商品
			this.getHotShop()
			// 获取活动列表
			this.getActive()
			//轮播图
			this.carousel()
			
		},
		methods: {  
			// 跳转到详情页面
			toDeatail(e){
				var id = e.currentTarget.dataset.index;
				uni.navigateTo({
					url:'../shop/shop-details?id='+id
				})
			},
			// 轮播图
			 carousel(){
				var that =this;
				uni.request({
					url:"https://wx.zzsnqyjxh.org/test/app/appGoodsImage/findByImg",
					method:"GET",
					success(res) {
						var Swiper = res.data.data.data
						that.swiper = Swiper;
					}
				})
			},
			// 跳转页面详情
			toPath(e){
				var targetId = e.currentTarget.dataset.index
				uni.navigateTo({
					url:"/pages/enroll/enroll-details?targetId="+targetId
				})
			},
			// 获取活动列表
			getActive(){
				var that = this;
				var url=" https://wx.zzsnqyjxh.org/test";
				var pageNum=1;
				var pageSize=1;
				if(pageNum<=pageSize){
					pageSize++
				}
				uni.request({
					url:'https://wx.zzsnqyjxh.org/test/app/huodong/findPage',
					data:{
						pageNum:pageNum,
						pageSize:pageSize
					},
					method:"POST",
					success: (res) => {
						var activeList = res.data.data.content;
						that.activeList = activeList;
					}
				})
				
			},
			// 点击加载更多加载更多商品
				loadingMore(){
					var that =this;
					var url=" https://wx.zzsnqyjxh.org/test"
					uni.request({
						url:"https://wx.zzsnqyjxh.org/test/app/Goods/findRecommend",
						method:"GET",
						success(res) {
							var hotShop = res.data.data.data;
							that.hotShop = hotShop;
							that.isShow=false;
							uni.showToast({
								title:"已经加载完毕",
								duration:1500
							})
						}
					})
				},
			// 获取最热门商品
			getHotShop(){
				var that =this;
				uni.request({
					url:"https://wx.zzsnqyjxh.org/test/app/Goods/findRecommend",
					method:"GET",
					success(res) {
						console.log(res)
						var hotShop = res.data.data.data;
						var newArr =hotShop.slice(0,4)
						that.hotShop = newArr
					}
				})
			},
			
		}
	}
